html,
body,
.app {
    height: 100vh;
    background: #eee;
    padding: 1em;
}

/*SidePanel.js*/
.menu {
    padding-bottom: 2em;
}

/* Messages.js */
.messages {
    height: 62vh;
    overflow-y: scroll;
}

.messages__progress {
    height: 58vh;
    overflow-y: scroll;
}

/* MessageForm.js */
.message__form {
    position: fixed !important;
    bottom: 1em;
    margin-left: 320px !important;
    left: 0;
    right: 1em;
    z-index: 200;
}

.emojipicker {
    position: absolute;
}

/* Message.js*/
.message__self {
    border-left: 2px solid orange;
    padding-left: 8px;
}

.message__image {
    padding: 1em;
}

/*ProgressBar.js*/

.progress__bar{
    margin: 0.3em 0 0 0 !important;
}

/*ColorPanel.js*/
.color__container {
    cursor: pointer;
    position: relative;
    overflow: hidden;
    width: 35px;
    border-radius: 3px;
}

.color__square {
    width: 35px;
    height: 35px;
}

.color__overlay {
    width: 85px;
    height: 35px; 
    -webkit-transform: rotate(225deg); 
        -ms-transform: rotate(225deg); 
            transform: rotate(225deg);
}

/*Typing.js*/
.user__typing {
    font-style: italic;
    font-weight: bold;
    margin-right: 3px;
}

.typing {
    width: 5em;
    height: 2em;
    position: relative;
    padding: 10px;
    margin-left: 5px;
    background: #e6e6e6;
    border-radius: 20px;
}

.typing__dot {
    float: left;
    width: 8px;
    height: 8px;
    margin: 0 4px;
    background: #8d8c91;
    border-radius: 50%;
    opacity: 0;
    -webkit-animation: loadingFade 1s infinite;
            animation: loadingFade 1s infinite;
}

.typing__dot:nth-child(1){
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

.typing__dot:nth-child(2){
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
} 

.typing__dot:nth-child(3){
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

@-webkit-keyframes loadingFade {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.8;
    }
    100% {
        opacity: 0;
    }
}

@keyframes loadingFade {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.8;
    }
    100% {
        opacity: 0;
    }
}

/*Skeleton.js*/

.skeleton {
    position: relative;
    overflow: hidden;
    height: 40px;
    margin-bottom: 10px;
}

.skeleton::after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    -webkit-animation: sweep 2s infinite;
            animation: sweep 2s infinite;
    background-image: -webkit-gradient(
        linear,
        right top, left top,
        from(transparent),
        color-stop(rgba(255, 255, 255, 0.4)),
        to(transparent)
    );
    background-image: -webkit-linear-gradient(
        right,
        transparent,
        rgba(255, 255, 255, 0.4),
        transparent
    );
    background-image: -o-linear-gradient(
        right,
        transparent,
        rgba(255, 255, 255, 0.4),
        transparent
    );
    background-image: linear-gradient(
        to left,
        transparent,
        rgba(255, 255, 255, 0.4),
        transparent
    )
}

@-webkit-keyframes sweep {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    50% {
        -webkit-transform: translateX(150%);
                transform: translateX(150%);
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}

@keyframes sweep {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    50% {
        -webkit-transform: translateX(150%);
                transform: translateX(150%);
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}

.skeleton__avatar {
    height: 35px;
    width: 35px;
    border-radius: 3px;
    background-color: rgba(58,57,57,0.3);
}

.skeleton__author {
    background-color: rgba(58,57,57,0.3);
    width: 120px;
    height: 10px;
    border-radius: 3px;
    position: absolute;
    bottom: 30px;
    left: 40px;
    right: 0;
}

.skeleton__details {
    background-color: rgba(58,57,57,0.3);
    height: 20px;
    border-radius: 3px;
    position: absolute;
    bottom: 5px;
    left: 40px;
    right: 20px;
}




